import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";
import {CashByCodeServiceProvider} from "../../providers/cash-by-code-service/cash-by-code-service";


@IonicPage()
@Component({
  selector: 'page-cash-by-code-add-beneficiary01',
  templateUrl: 'cash-by-code-add-beneficiary01.html',
})
export class CashByCodeAddBeneficiary01Page {

  formVal = {mobileNo: "",name : "", nid : ""};
  cashByCodeAddBeneficiary01Error = "";
  mobileNo;
  name;
  nid;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public tokenService: TokenServiceProvider,
              public storage: LocalStorageProvider,
              public maskTokenUtil: MaskTokenProvider,
              public cryptoUtil: CryptoParamUtilProvider,
              public cashByCodeService: CashByCodeServiceProvider,
              public alertCtrl : AlertController,
              public gaProvider : GoogleAnalyticsProvider) {
  }

  AddICashRemitAccount01Action = function () {

    this.cashByCodeAddBeneficiary01Error = "";
    let flag = true;
    this.mobileNo=this.formVal.mobileNo;
    this.name=this.formVal.name;
    this.nid=this.formVal.nid;

    if (this.mobileNo == "" || typeof(this.mobileNo) == "undefined") {
      this.cashByCodeAddBeneficiary01Error = "Beneficiary mobile no. is required.";
      flag = false;
      return;
    }
    if (this.name == "" || typeof(this.name) == "undefined") {
      this.cashByCodeAddBeneficiary01Error = "Beneficiary Name is required.";
      flag = false;
      return;
    }

    // if (this.nid == "" || typeof(this.nid) == "undefined") {
    //   this.cashByCodeAddBeneficiary01Error = "NID number is required.";
    //   flag = false;
    //   return;
    // }

    if (flag) {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }
  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let encMobileNo = this.cryptoUtil.getEncryptVal(this.mobileNo);
      let encName = this.cryptoUtil.getEncryptVal(this.name);
      let encNID = this.cryptoUtil.getEncryptVal(this.nid);
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.cashByCodeService.addCashByCodeBeneficiary(unMaskToken, appData.userId, appData.appId, encMobileNo, encName, encNID)
        .then(data => {
          this.BeneficiaryAccountAddComplete(data);
        });
    }
  };

  BeneficiaryAccountAddComplete = function (data) {

    if (data.statusCode == 1) {
      this.navCtrl.push('CashByCodeAddBeneficiary02Page', {'subscriptionDetails': JSON.stringify(data.object)});
    }

    else if (data.statusCode == 0) {
      this.cashByCodeAddBeneficiary01Error = data.note;
    }

    else {
      //$log.info("else Loop");
      this.cashByCodeAddBeneficiary01Error = "System Error Contact with Administrator.";
    }
  };

  ionViewDidLoad() {
    // console.log('ionViewDidLoad AddCreditAccount01Page');
    this.gaProvider.startTrackerWithId("Add Cash by code beneficiary");
  }

}



